<template>
  <el-container class="content">
    <el-header>
      <HeaderTitle class="header-title" :titleName="CONSTANTS_DEVICE_CONFIGURATION.ADD_DEVICE">
      </HeaderTitle>
    </el-header>
    <el-main>
      <div class="area-main">
 <el-main>
      <div class="area-main">
        <div class="area-aside">
          <div class="area-tree">
            <div class="area-tabs">
              <el-radio-group v-model="tabPosition">
                <el-radio-button label="group">{{CONSTANTS_DEVICE_CONFIGURATION.WORKSHOP_GROUP}}</el-radio-button>
                <el-radio-button label="type">{{CONSTANTS_DEVICE_CONFIGURATION.DEVICE_TYPE}}</el-radio-button>
              </el-radio-group>
            </div>
            <div class="area-zTree">
              <ZTree
                :nodes="nodeData"
                @ztreeOnClick="ztreeOnClick"
                @ztreeHandleCreated="ztreeHandleCreated"
              ></ZTree>
            </div>
          </div>
        </div>
        <div class="area-info">
          <template>
            <div class="userInformation">
              <!-- <HeaderTitle class="header-title" :titleName="HeadTitleName">
                <div slot="head-search" class="head-search">{{HeadTitleUrl}}</div>
              </HeaderTitle> -->
              <VXETable
                id="table"
                :tableName="tableName"
                :tableLoading="tableLoading"
                :pager-config="tablePage"
                :tableDatas="tableDatas"
                :tableColumns="tableColumns"
                :tablePage="tablePage"
                @onTablePageChange="onTablePageChange"
                @chooseCheck="tableChooseCheck"
                ref="xGrid"
              ></VXETable>
              <div class="btnDiv">
                <el-button type="primary" @click="submitEdit">{{BTN_TEXT.SAVE}}</el-button>
                <el-button @click="goBack">{{BTN_TEXT.RETURN}}</el-button>
              </div>
            </div>
          </template>
        </div>
      </div>
    </el-main>
        <!-- <vxe-grid
            border="full"
            resizable
            keep-source
            ref="xGrid"
            class="vxe-table-element"
            height="550"
            :edit-rules="validRules"
            :columns="tableColumns"
            :data="tableDatas"
            :edit-config="{trigger: 'click', mode: 'row', showStatus: true, icon: 'fa fa-file-text-o'}"
          >
          </vxe-grid>
          <div class="btnDiv">
            <el-button type="primary" @click="fullValidEvent">{{BTN_TEXT.SAVE}}</el-button>
            <el-button @click="goBack">{{BTN_TEXT.RETURN}}</el-button>
        </div>-->
      </div>
    </el-main>
  </el-container>
</template>

<script>
import js from '@/assets/js/main/QualityConfiguration/addInfo';
export default js;
</script>

<style lang="less" scoped>
@import '../../../assets/less/main/QualityConfiguration/addInfo.less';
</style>
