import HeaderTitle from '@/components/common/PageNavBar/HeaderTitle';
// 引入ztree树形组件
import ZTree from '@/components/common/ZTree/ZTree';
import {
  MSG,
  STATUS_CODE,
  TITLE,
  BTN_TEXT,
  CONSTANTS_MATERIAL
} from '@/utils/constantsFile';
import { showErrorNotify } from '@/utils/notify';
import {
  materialTypeGetTreeFunc
} from '@/utils/publicFunction';
import {
  showWarningMessage,
  showErrorMessage,
  showSuccessMessage
} from '@/utils/message';
import {
  materialsTypeInsertOrUpdateBatchAction,
  materialsTypeDeleteByIdAction
} from '@/api/material';
// 拖拽列表组件
import VueDraggable from 'vuedraggable';

/* import axios from 'axios';
import qs from 'qs'; */

export default {
  name: 'PageModifyUserGroup',
  components: {
    HeaderTitle,
    VueDraggable,
    ZTree
  },
  data() {
    return {
      CONSTANTS_MATERIAL: CONSTANTS_MATERIAL,
      BTN_TEXT: BTN_TEXT,
      MSG: MSG,
      pageTitle: CONSTANTS_MATERIAL.CONFIG_PAGE,
      cardTitle: CONSTANTS_MATERIAL.CONFIG_CARD,
      panelTitle: CONSTANTS_MATERIAL.CONFIG_PANEL,
      // tab切换
      tabPosition: 'group',
      treeData: [],
      defaultProps: {
        children: 'children', // 子节点属性的别名
        label: 'name' // 在节点展示的属性字段
      },
      // 保存当前展示的节点信息
      ztreeSelectedNode: {},
      // ztree树形对象
      ztreeObj: {},
      // ztree选中节点对象
      //   ztreeSelectedNode: {},
      // ztree数据
      nodeData: [],
      isInitTree: true,
      // 保存变动的子级部门集合
      tempChildren: []
    };
  },
  watch: {
    // 切换车间分组和设备类型查询
    tabPosition() {
      this.isInitTree = true;
      this.getTreeData();
    }
  },
  computed: {
    // 保存默认展开的一级节点的id，用于树组件默认展开
    defaultExpandNodeKey() {
      const arr = [];
      if (Array.isArray(this.treeData) && this.treeData.length > 0) {
        this.treeData.map(({ id }) => {
          arr.push(id);
        });
      }
      return arr;
    }
  },

  methods: {
    /**
     * @description: 初始化获取ztree
     * @param obj ztree对象
     * @return: 无
     * @author: ZJM
     */
    ztreeHandleCreated(obj) {
      // 获取ztree对象
      this.ztreeObj = obj;
      if (this.ztreeSelectedNode.id >= -1) {
        var nodes = this.ztreeObj.getNodesByParam(
          'id',
          this.ztreeSelectedNode.id,
          null
        );
        this.ztreeSelectedNode = nodes[0];
        // 选中节点
        this.ztreeObj.selectNode(nodes[0]);
        // 显示子集列
        this.getCurrentNode(nodes[0]);
        // 展开选中项
        this.ztreeObj.expandNode(nodes[0], true);
      }
    },
    /**
     * @description: ztree点击事件
     * @param 无
     * @return: 无
     * @author: ZJM
     */
    ztreeOnClick(event, nodes, obj) {
      if (nodes) {
        this.ztreeSelectedNode = nodes;
      }
      if (this.tabPosition === 'group' && nodes.level === 3) {
        this.tempChildren = [];
      } else {
        // 如果节点有children属性
        if (nodes && nodes.children) {
          // this.tempChildren = [...data.children];
          this.tempChildren = JSON.parse(JSON.stringify(nodes.children));
        } else {
          this.tempChildren = [];
        }

        this.fillArrayToTargetNumber(this.tempChildren, 14);
      }
    },
    /**
     * @description: 对数组进行填充处理
     * @param {array} arr 用于展示下级部门的数组
     * @param {number} num 数组的目标长度
     * @return: void
     * @author: cyb
     */
    fillArrayToTargetNumber(arr, num) {
      if (Array.isArray(arr)) {
        const length = arr.length;

        // 如果子级部门少于目标数量，填充空的树节点对象，直到数组长度达到target数量
        if (length < num) {
          for (let index = 0; index < num - length; index++) {
            arr.push({
              name: '',
              pid: this.ztreeSelectedNode.id
            });
          }
        }
      }
    },

    /**
     * @description: 根据节点数据，初始化当前展示的部门
     * @param {object} node 节点数据
     * @return: void
     * @author: cyb
     */
    getCurrentNode(node) {
      this.ztreeSelectedNode = node;
      if (node.children) {
        this.tempChildren = JSON.parse(JSON.stringify(node.children));
        // this.tempChildren = [...node.children];
      } else {
        this.tempChildren = [];
      }
      this.fillArrayToTargetNumber(this.tempChildren, 14);
    },

    /**
     * @description: 获取树组件数据
     * @param 无
     * @return: void
     */
    async getTreeData() {
      var _that = this;
      try {
        const res = await materialTypeGetTreeFunc();
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          const groupData = [
            {
              id: -1,
              pId: 'x',
              type: 0,
              label: CONSTANTS_MATERIAL.ALL_TYPE,
              name: CONSTANTS_MATERIAL.ALL_TYPE,
              children: []
            }
          ];
          groupData[0].children = res.rows;
          this.nodeData = groupData;
          this.$nextTick(() => {
            // 如果是页面第一次初始化
            if (this.isInitTree) {
              _that.isInitTree = false;
              _that.getCurrentNode(_that.nodeData[0]);
            }
          });
        } else {
          // 提示异常信息
          showWarningMessage(res.message);
        }
      } catch (error) {
        showErrorNotify(TITLE.NOTIFY, error);
      } finally {
      }
    },

    /**
     * @description: “新增”按钮点击方法
     * @param {number} index 当前行的index
     * @return: void
     * @author: cyb
     */
    handleNodeAddEvent(index) {
      this.tempChildren.splice(index + 1, 0, {
        name: '',
        pid: this.ztreeSelectedNode.id
      });
    },
    /**
     * @description: “删除”按钮点击方法
     * @param {number} index 当前行的index
     * @return: void
     * @author: cyb
     */
    async handleNodeDeleteEvent(index) {
      this.isInitTree = false;
      // 最后一个非空节点的index
      const lastValidNodeIndex = this.getLastValidNodeIndex();
      // 如果index大于lastValidNodeIndex，说明是所有非空节点后的空节点
      if (index > lastValidNodeIndex) {
        return false;
      }
      const data = this.tempChildren[index];
      // 如果id不存在，说明是空节点
      // 如果index小于lastValidNodeIndex，说明是在非空节点中插入的空节点
      if (!data.id) {
        this.tempChildren.splice(index, 1);
      } else {
        this.$confirm(MSG.CONFIRM_CHOOSE_DELETE, TITLE.PROMPT, {
          confirmButtonText: BTN_TEXT.CONFIRM,
          cancelButtonText: BTN_TEXT.CANCEL,
          type: 'warning'
        })
          .then(() => {
            this.setDelete(data.id);
            this.tempChildren.splice(index, 1);
            this.tempChildren.push({
              name: '',
              pid: this.ztreeSelectedNode.id
            });
          })
          .catch(() => {
            /* 此处可以提示消息 */
          });
      }
    },
    async setDelete(id) {
      try {
        const res = await materialsTypeDeleteByIdAction({
          id: id
        });
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          showSuccessMessage(res.message);
          this.getTreeData();
        } else {
          // 提示异常信息
          showWarningMessage(res.message);
        }
      } catch (error) {
        showErrorMessage(error);
      }
    },
    /**
     * @description: 调用接口，提交修改后的数据
     * @param {array} data 修改后的数据
     * @return: void | promise
     * @author: cyb
     */
    async postModifiedData(data) {
      try {
        const res = await materialsTypeInsertOrUpdateBatchAction({}, data);
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          showSuccessMessage(res.message);
          this.getTreeData();
        } else {
          // 提示异常信息
          showWarningMessage(res.message);
        }
      } catch (error) {
        showErrorMessage(error);
      }
    },

    /**
     * @description: 获取数组最后一个非空节点的索引
     * @param 无
     * @return: number
     * @author: cyb
     */
    getLastValidNodeIndex() {
      let lastNotEmptyNodeIndex = this.tempChildren.length - 1;
      while (
        this.tempChildren[lastNotEmptyNodeIndex].name.trim() === '' &&
        lastNotEmptyNodeIndex >= 0
      ) {
        lastNotEmptyNodeIndex--;
      }

      return lastNotEmptyNodeIndex;
    },

    /**
     * @description: “保存”按钮点击事件
     * @param 无
     * @return: void
     * @author: cyb
     */
    handleSaveEvent() {
      // 获取最后一个非空节点的索引
      const index = this.getLastValidNodeIndex();

      // 如果数组为空，不做提交
      if (index < 0) {
        showWarningMessage('请先填写数据再保存！');
        return false;
      }

      // 判断最后一个非空节点前是否还有空节点，如果存在则不提交
      const hasEmptyNode = this.tempChildren.some(
        ({ name }, key) => name.trim() === '' && key < index
      );

      if (hasEmptyNode) {
        showWarningMessage('请删除中间的空行,或填写完整再保存！');
        return false;
      }

      const data = [];

      // 构造提交给后端的数据
      for (let i = 0; i <= index; i++) {
        const item = {};
        const { name } = this.tempChildren[i];
        // 字段要重命名

        item.materialsTypeParentId = this.ztreeSelectedNode.id;
        item.sort = i + 1;
        item.materialsTypeName = name;
        if (this.tempChildren[i].id) {
          item.materialsTypeId = this.tempChildren[i].id;
        }
        data.push(item);
      }

      // 表示树组件已经修改
      this.isInitTree = false;
      this.postModifiedData(data);
    },
    inputNull(index) {
      if (this.tempChildren[index].name === '') {
        this.tempChildren[index].name = this.ztreeSelectedNode.children[index].name;
        showWarningMessage('不允许输入空值！');
      }
    },
    /**
     * @description: “返回”按钮点击事件
     * @param null
     * @return: void
     * @author: cyb
     */
    returnMainPage() {
      this.$emit('goBack', 3);
    }
  },

  created() {
    this.getTreeData();
  }
};
