<template>
  <el-container>
    <el-header class="border-bottom-solid">
      <HeaderTitle class="header-title" :flag="false" :title-name="pageTitle"></HeaderTitle>
    </el-header>
    <el-main>
      <div class="main-area">
        <el-card class="tree-card" shadow="never" :body-style="{ padding: '0px' }">
          <div slot="header" class="tree-card-header">
            <header-title :flag="false" :title-name="cardTitle"></header-title>
          </div>
          <div class="area-ztree">
            <!-- 树组件 -->
            <ZTree
              ref="ztree"
              :nodes="nodeData"
              :isOneExpendAll="true"
              @ztreeOnClick="ztreeOnClick"
              @ztreeHandleCreated="ztreeHandleCreated"
            ></ZTree>
            <!-- <el-tree
              class="custom-tree"
              :data="treeData"
              :props="defaultProps"
              ref="dataTree"
              style="margin: 10px"
              node-key="id"
              check-on-click-node
              :default-expanded-keys="defaultExpandNodeKey"
              @node-click="handleNodeClick"
            ></el-tree>-->
          </div>
        </el-card>
        <el-card class="operation-panel" shadow="never" :body-style="{ padding: '0px' }">
          <header-title class="header-title" :flag="false" :title-name="panelTitle"></header-title>
          <section class="operation-panel__main">
            <aside class="aside">
              <span class="parent-department">{{ ztreeSelectedNode.name}}</span>
              <span class="font-bold" v-if="tempChildren.length!==0">></span>
            </aside>
            <main>
              <vue-draggable
                animation="380"
                ghostClass="ghost"
                v-model="tempChildren"
                handle=".move-row"
              >
                <li v-for="(dep, index) in tempChildren" class="node-row" :key="index">
                  <el-input
                    class="node-input"
                    size="small"
                    v-model="dep.name"
                    @blur="inputNull(index)"
                  ></el-input>
                  <!-- 移动节点按钮 -->
                  <el-button
                    :title="BTN_TEXT.MOVES"
                    icon="node-move"
                    class="icon-button move-row"
                    type="text"
                  ></el-button>
                  <!-- 新增节点按钮 -->
                  <el-button
                    :title="BTN_TEXT.NEW"
                    icon="node-add"
                    class="margin-left-0 margin-right-10 icon-button padding-bottom-2"
                    type="text"
                    @click="handleNodeAddEvent(index, dep)"
                  ></el-button>
                  <!-- 删除节点按钮 -->
                  <el-button
                    :title="BTN_TEXT.DELETE"
                    icon="node-delete"
                    class="icon-button margin-left-0"
                    type="text"
                    @click="handleNodeDeleteEvent(index)"
                  ></el-button>
                </li>
              </vue-draggable>
            </main>
          </section>
          <el-button class="custom-button" @click="returnMainPage">{{BTN_TEXT.RETURN}}</el-button>
          <el-button
            v-if="tempChildren.length!==0"
            type="primary"
            class="custom-button"
            style="margin-right: 20px;margin-top:30px;margin-bottom:20px"
            @click="handleSaveEvent"
          >{{BTN_TEXT.SAVE}}</el-button>
        </el-card>
      </div>
    </el-main>
  </el-container>
</template>

<script>
import js from '@/assets/js/main/MaterialManagement/materialTypeConfig';
export default js;
</script>

<style lang="less" scoped>
@import '../../../assets/less/main/MaterialManagement/materialTypeConfig.less';
</style>
