// 引入标题组件
import HeaderTitle from '@/components/common/PageNavBar/HeaderTitle';
// 引入标题元素组件
import HeadOperationItem from '@/components/common/PageNavBar/HeadOperationItem';
// 导入所需接口
// 引入通用接口
import {
  STATUS_CODE,
  TITLE,
  MSG,
  BTN_TEXT,
  CONSTANTS_DEVICE_CONFIGURATION,
  VARIABLE,
  CONSTANTS_MATERIAL,
  FORM_CONFIG_DATE
} from '@/utils/constantsFile';
// 引入ztree树形组件
import ZTree from '@/components/common/ZTree/ZTree';
// 引入table组件
import VXETable from '@/components/common/Table';
// 引入提示组件
import { showWarningMessage } from '@/utils/message';
import { showErrorNotify } from '@/utils/notify';
// 导入日期处理类库
// import moment from 'moment';
// 导入所需接口
import {
  deviceSelectDeviceByConditionAction,
  deviceGroupGetTreeAction,
  deviceWorkshopTypeGetTreeAction,
  getPageDeviceByDetectionDeviceCode
} from '@/api/management';
// import { showSuccessMessage, showWarningMessage } from '@/utils/message';
// import { showErrorNotify } from '@/utils/notify';
// import moment from 'moment';
export default {
  components: {
    // 加载标题组件
    HeaderTitle,
    ZTree,
    HeadOperationItem,
    VXETable
  },
  props: {
    deviceCode: String
  },
  data() {
    // 扩展字段通用验证
    const setValidateForm = ({ column, row, cellValue }) => {
      return new Promise((resolve, reject) => {
        return this.$options.filters.formatterTableValidate(
          column,
          cellValue,
          this.extendPropertyVoList,
          resolve,
          reject
        );
      });
    };

    return {
      setValidateForm: setValidateForm,
      FORM_CONFIG_DATE: FORM_CONFIG_DATE,
      VARIABLE: VARIABLE,
      BTN_TEXT: BTN_TEXT,
      CONSTANTS_DEVICE_CONFIGURATION: CONSTANTS_DEVICE_CONFIGURATION,
      imageUrl: '',
      // 页面标题
      titleName: CONSTANTS_MATERIAL.ADD_MATERIAL,
      // 表单验证
      validRules: {},
      deviceName: '',
      // 树导航标题
      HeadTitleUrl: '',
      // table标题
      HeadTitleName: CONSTANTS_DEVICE_CONFIGURATION.DEVICE_LIST,
      // 车间分组/类型 ids
      searchIdArr: [],
      // table多选数据
      deviceChecks: [],
      tableName: 'device',
      // tab切换
      tabPosition: 'group',
      // 新增时显示模板选择
      showModelItem: false,
      // 父类型绑定的参数
      fatherNodeData: [],
      // 用于操作当前显示哪个组件 0：主页面 1：新增子节点参数
      showPage: 0,
      // 模板下拉
      modelOptions: [],
      // 表格查询表单数据
      formParams: {
        model: null
      },
      // 查询表单数据
      formInline: {
        cnName: '',
        shortName: '',
        parameterName: '',
        isWarning: 'null'
      },
      // 表单验证
      rules: {
        name: [
          { required: true, message: MSG.PLEASE_ENTER_CONTENT, trigger: 'blur' }
        ]
      },
      // 操作标记 1、新增同级  2、新增下级 3、修改
      operaType: 1,
      // label文字长度
      formLabelWidth: '150px',
      // label文字短长度
      formLabelWidthShort: '50px',
      // 控制右键菜单的显示项
      rightMenu: [],
      // 表单数据
      form: {
        name: ''
      },
      // 模态框名称
      titlename: '',
      dialogVisibleQrCode: false,
      // 控制模态框修改
      dialogVisibleUpdate: false,
      // 控制模态框是【节点维护】否显示
      dialogVisible: false,
      // ztree树形对象
      ztreeObj: {},
      // ztree选中节点对象
      ztreeSelectedNode: {
        id: '-1',
        pId: '-1'
      },
      // ztree树形数据
      nodeData: [],
      // table加载动画
      tableLoading: false,
      // table分页配置
      tablePage: {
        total: 0,
        currentPage: 1,
        pageSize: 10,
        pageSizes: [5, 10, 20, 50, 100, 200, 500],
        layouts: [
          'Sizes',
          'PrevJump',
          'PrevPage',
          'Number',
          'NextPage',
          'NextJump',
          'FullJump',
          'Total'
        ],
        perfect: true,
        slots: {
          // 使用自定义插槽
          left: 'pager_left'
        }
      },
      // table数据
      tableDatas: [],
      // table表头配置
      tableColumns: [
        {
          type: 'checkbox',
          width: '50',
          fixed: 'left',
          align: 'center'
        },
        {
          field: 'deviceName',
          title: CONSTANTS_DEVICE_CONFIGURATION.DEVICE_NAME,
          align: 'center'
        },
        {
          field: 'deviceCode',
          title: CONSTANTS_DEVICE_CONFIGURATION.DEVICE_CODE,
          align: 'center'
        },
        {
          field: 'mode',
          title: '工厂',
          align: 'center'
        },
        {
          field: 'typeName',
          title: '车间',
          align: 'center'
        },
        {
          field: 'groupName',
          title: '产线',
          align: 'center'
        },
        {
          field: 'groupName',
          title: '设备类型',
          align: 'center'
        }
      ],
      contrastData: []
    };
  },
  computed: {},
  created() {
    // 调用查询树形数据接口
    this.getTree();
    // 调用设备列表查询
    this.findList();
  },
  mounted() {
    // this.initExtendTable();
  },
  watch: {
    deviceData() {
    },
    // 切换车间分组和设备类型查询
    tabPosition() {
      // 表单数据
      this.form = {
        name: ''
      };
      // ztree选中节点对象
      this.ztreeSelectedNode = {
        id: '-1',
        pId: '-1'
      };

      this.getTree();
    }
  },
  methods: {
    /**
     * @description: 返回按钮点击
     * @param 无
     * @return:无
     * @author: ZJM
     */
    goBack() {
      this.$emit('goBack', this.GLOBAL.VARIABLE.ZERO_NUMBER);
    },
    tableChooseCheck() {},
    /**
     * @description: ztree单击事件
     * @param event 事件对象
     * @param nodes 选中数据对象
     * @param obj ztree对象
     * @return: 无
     * @author: ZJM
     */
    ztreeOnClick(event, nodes, obj) {
      // 遍历获取父级name和子级节点id
      this.searchIdArr = [];
      var nameUrl = [];
      nameUrl.push(nodes.name);
      var node = nodes.getParentNode();
      while (node) {
        nameUrl.push(node.name);
        node = node.getParentNode();
      }
      this.HeadTitleUrl = nameUrl.reverse().join(' / ');
      this.searchIdArr = this.getChildren(this.searchIdArr, nodes);

      // 获取选中数据
      this.ztreeSelectedNode = nodes;
      // 获取ztree对象
      this.ztreeObj = obj;
      // 重置页数
      this.tablePage.currentPage = 1;
      // 调用查询
      this.findList();
    },
    /**
     * @description: 递归获取子级数据
     * @param row 行数据
     * @return: 无
     * @author: ZJM
     */
    getChildren(ids, treeNode) {
      ids.push(treeNode.id);
      if (treeNode.isParent) {
        for (var obj in treeNode.children) {
          this.getChildren(ids, treeNode.children[obj]);
        }
      }
      return ids;
    },
    /**
     * @description: ztree初始化完成事件
     * @param obj ztree对象
     * @return: 无
     * @author: ZJM
     */
    ztreeHandleCreated(obj) {
      // 获取ztree对象
      this.ztreeObj = obj;
    },
    /**
     * @description: table分页
     * @param currentPage 第几页
     * @param pageSize 一页几条
     * @return: 无
     * @author: ZJM
     */
    onTablePageChange(currentPage, pageSize) {
      // 点击页数写入table分页配置
      this.tablePage.currentPage = currentPage;
      // 一页条数夜路table分页配置
      this.tablePage.pageSize = pageSize;
      // 调用查询接口
      this.findList();
    },
    /**
     * @description: 查询所属参数
     * @param val
     * @return: 无
     * @author: ZJM
     */
    async findList() {
      try {
        // 显示table加载动画
        this.tableLoading = true;

        const params = {
          current: this.tablePage.currentPage,
          size: this.tablePage.pageSize,
          deviceStateList: [1, 3, 0]
        };
        if (this.searchIdArr.length > 0) {
          if (this.tabPosition === 'group') {
            params.groupIdList = this.searchIdArr;
          } else if (this.tabPosition === 'type') {
            params.typeIdList = this.searchIdArr;
          }
        }

        const res = await deviceSelectDeviceByConditionAction(params);
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          // 将数据写入表单
          this.tableDatas = res.rows;
          this.tablePage.total = res.total;
          this.tableLoading = false;
          this.toFindCheck(res.rows);
        } else {
          // 提示异常信息
          showWarningMessage(res.message);
        }
      } catch (error) {
        showErrorNotify(TITLE.NOTIFY, error);
      } finally {
      }
    },
    /**
     * @description: 获取类型树数据
     * @param 无
     * @return: 无
     * @author: ZJM
     */
    async getTree() {
      if (this.tabPosition === 'group') {
        // 若是车间分组
        try {
          const res = await deviceGroupGetTreeAction();
          if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
            this.nodeData = res.rows;
          } else {
            // 提示异常信息
            showWarningMessage(res.message);
          }
        } catch (error) {
          showErrorNotify(TITLE.NOTIFY, error);
        } finally {
        }
      } else if (this.tabPosition === 'type') {
        // 若是设备类型
        try {
          const res = await deviceWorkshopTypeGetTreeAction();
          if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
            this.nodeData = res.rows;
          } else {
            // 提示异常信息
            showWarningMessage(res.message);
          }
        } catch (error) {
          showErrorNotify(TITLE.NOTIFY, error);
        } finally {
        }
      }
    },
    /**
     * @description: 查询用户绑定的设备
     * @param data 设备数据
     * @return: 无
     * @author: ZJM
     */
    async toFindCheck(data) {
      try {
        const res = await getPageDeviceByDetectionDeviceCode({
          current: 1,
          size: 100000,
          detectionDeviceCode: this.deviceCode
        });
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          this.contrastData = res.rows;
          for (var i in data) {
            for (var j in res.rows) {
              if (data[i].deviceCode === res.rows[j].deviceCode) {
                this.$refs.xGrid.toggleCheckboxRow(data[i]);
              }
            }
          }
        } else {
          // 提示异常信息
          showWarningMessage(res.message);
        }
      } catch (error) {
        showErrorNotify(TITLE.NOTIFY, error);
      } finally {
      }
    },
    /**
     * @description: 保存关联
     * @param 无
     * @return: 无
     * @author: ZJM
     */
    submitEdit() {
      const checkedData = this.$refs.xGrid.getCheckboxRecords();
      if (this.isFunc) {
        this.$emit(
          'saveRelatedDevice',
          checkedData,
          this.tableDatas,
          this.contrastData
        );
      } else {
        this.$emit('saveRelatedDevice', checkedData, this.tableDatas);
      }
    }
    // /**
    //  * @description: 关闭模态框
    //  * @param 无
    //  * @return: 无
    //  * @author: ZJM
    //  */
    // cancelBtnClick() {
    //   this.$emit('hiddenRelated');
    // }
    // /**
    //  * @description: 返回
    //  * @param val 0为显示index组件
    //  * @return:
    //  * @author: ZJM
    //  */
    // goBack(val) {
    //   // 显示组件
    //   this.showPage = val;
    //   // 调用查询接口
    //   this.findList();
    // }
  }
};
